import React, { Component } from 'react';
import { Layout, Menu, Button, Drawer, Divider, Affix, Popover } from 'antd';
import Link from 'next/link';
import Fade from 'react-reveal/Fade';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mobileMenu: false,
      fixed: false,

      loggedIn: false,
      daysLeft: 13,
      userName: 'John Doe',
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
  }

  onScroll = (e) => {
    let top;
    top = document.documentElement.scrollTop;
    if (top > 64) {
      document.getElementById("navbar").classList.contains('fixed') ? null : document.getElementById("navbar").classList.add('fixed') 
    }
    else {
      document.getElementById("navbar").classList.contains('fixed') ? document.getElementById("navbar").classList.remove('fixed') : null
    }
  };

  render() {
    let { fixed, loggedIn, daysLeft, userName } = this.state;
    return (
      <React.Fragment>
        {/* <div className="banner mobile-hide">
          Whatsapp is on preview now! Pre-built bots will be ready to test. Stay tuned.
        </div> */}
        {/* <Affix offsetTop={0} onChange={affixed => this.setState({ fixed: affixed })}> */}
        <Layout.Header className={`${fixed ? 'fixed' : ''}`} ref={el => this.navbar = el} id="navbar" >
          <div className="container">
            <Fade left>
              <Link href="/">
                <a>
                  <img src="/static/img/logo/logo-dark.svg" />
                </a>
              </Link>
            </Fade>
            <Menu
              theme="light"
              mode="horizontal"
              defaultSelectedKeys={[this.props.router.pathname.substring(1, this.props.router.pathname.length)] || ['']}

            >
              <Menu.Item className="font-bilgetech" key="features"><Link href="/features"><a>Features </a></Link></Menu.Item>
              <Menu.Item className="font-bilgetech" key="integrations"><Link href="/integrations"><a>Integrations </a></Link></Menu.Item>

              <Menu.SubMenu title={<span className="submenu-title-wrapper font-bilgetech">Solutions </span>}>
                <Menu.Item key="setting:1"><Link href="/solutions/e-commerce"><a>E-Commerce</a></Link></Menu.Item>
                <Menu.Item key="setting:2"><Link href="/solutions/lead-generation"><a>Lead Generation</a></Link></Menu.Item>
                <Menu.Item key="setting:3"><Link href="/solutions/customer-support"><a>Customer Support</a></Link></Menu.Item>
                <Menu.Item key="setting:4"><Link href="/solutions/hr-process"><a>HR Process</a></Link></Menu.Item>
              </Menu.SubMenu>


              <Menu.Item className="font-bilgetech" key="pricing"><Link href="/pricing"><a>Pricing </a></Link></Menu.Item>
              {/* <Menu.Item className="font-bilgetech" key="blog"><Link href="/blog"><a>Blog</a></Link></Menu.Item> */}
            </Menu>
            <Fade right>
              <div>
                {
                  !loggedIn ?
                    <React.Fragment>
                      <Button type="primary">
                        Start free trial
                        <img className="ml-8" src="/static/img/icon/right.svg" />
                      </Button>
                      <Button className="btn-link ml-24 mp-24 p-8 text-thick" onClick={() => this.setState({ loggedIn: true })}>
                        Sign in
                  </Button>
                    </React.Fragment>
                    :
                    <React.Fragment>
                      <div className="header-logged-in">
                        <span className="days">{daysLeft}</span>
                        <div>
                          <span>days left </span>
                          <span>in trial period</span>
                        </div>
                        <div className="username">
                          {userName}
                        </div>
                        <Popover placement="bottom" overlayClassName="header-popover" content={
                          <div>
                            <div>
                              <Link href="https://app.botgate.ai"><a>Dashboard </a></Link>
                            </div>
                            <div>
                              <Link href="/features"><a onClick={(e) => { e.stopPropagation(); this.setState({ loggedIn: false }) }}> Logout </a></Link>
                            </div>
                          </div>

                        } trigger={['click', 'hover']}>
                          <div className="icon-drop">
                            <img className="ml-8" src="/static/img/icon/right.svg" />
                          </div>
                        </Popover>
                      </div>
                    </React.Fragment>
                }


              </div>
            </Fade>
          </div>


          <div className="mobile-header">
            <img src="/static/img/logo/logo-dark.svg" />
            <img onClick={() => this.setState({ mobileMenu: true })} className="svg-wrapper" src="/static/img/icon/menu.svg" />
            <Drawer
              title={<div className="mobile-header">
                <img src="/static/img/logo/logo-dark.svg" />
                <img onClick={() => this.setState({ mobileMenu: false })} className="svg-wrapper" src="/static/img/icon/close.svg" />
              </div>
              }
              placement="right"
              closable={false}
              onClose={() => this.setState({ mobileMenu: false })}
              visible={this.state.mobileMenu}
            >
              <div className="content text-right">
                <Fade right cascade>
                  <div>
                    <div>
                      <Button type="primary">
                        Start free trial
                      <img className="ml-8" src="/static/img/icon/right.svg" />
                      </Button>
                    </div>
                    <div className="mt-16">
                      <Button className="btn-link bg-none text-thick">
                        Sign in
                    </Button>
                    </div>
                    <Divider />
                    <h3 onClick={() => this.setState({ mobileMenu: false })}>
                      <Link href="/features">
                        <a>
                          Features
                   </a>
                      </Link>
                    </h3>
                    <h3 onClick={() => this.setState({ mobileMenu: false })}>
                      <Link href="/integrations">
                        <a>
                          Integrations
                  </a>
                      </Link>
                    </h3>
                    <h3 onClick={() => this.setState({ mobileMenu: false })}>
                      <Link href="/solutions">
                        <a>
                          Solutions
                  </a>
                      </Link>
                    </h3>
                    <h3 onClick={() => this.setState({ mobileMenu: false })}>
                      <Link href="/pricing">
                        <a>
                          Pricing
                  </a>
                      </Link>
                    </h3>
                    {/* <h3 onClick={() => this.setState({ mobileMenu: false })}>
                        <Link href="/blog">
                          <a>
                            Blog
                  </a>
                        </Link>
                      </h3> */}
                  </div>
                </Fade>
              </div>
            </Drawer>
          </div>


        </Layout.Header>
        {/* </Affix> */}
      </React.Fragment>
    );
  };
};

export default Header;